const app = getApp();
export default {
	data() {
		return {
			addressList: []
		};
	},
	onLoad() {
		
	},
	onShow() {
		this.getList()
		// var arr = uni.getStorageSync('addressList') || [];
		// console.info('缓存数据：' + arr);
		// // 更新数据
		// this.addressList = arr;
	},
	methods: {
		addAddress() {
			uni.navigateTo({
				url: `/pages/addersNew?type=1`
			});
		},
		getSel(e){
			uni.setStorageSync('address', e);
			uni.navigateBack({
				delta: 1
			})
		},
		revise(e){
			console.log(e)
			uni.navigateTo({
				url: `/pages/addersNew?type=2&obj=${JSON.stringify(e)}`
			});
		},
		getList(){
			uni.showLoading({
				title: "加载中...",
			})
			uni.request({
				url: `${this.$helper.requestUrl}user/getAddressList`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = this.$helper.null2str(res.data)
					console.log(res,'++++++++')
					if(res.status_code == 'ok'){
						this.addressList = res.data
					}
				}
			})
		}
		//   /* 删除item */
		//   delAddress (e) {
		//     this.data.addressList.splice(e.target.id.substring(3), 1);
		//     // 更新data数据对象
		//     if (this.data.addressList.length > 0) {
		//       this.addressList = this.data.addressList
		//       wx.setStorageSync('addressList', this.data.addressList);
		//     } else {
		//       this.setData({
		//         addressList: this.data.addressList
		//       })
		//       wx.setStorageSync('addressList', []);
		//     }
		//   }
	}
};
